
import Vue from 'vue'
import App from './App.vue'
import VueRouter from "vue-router"

Vue.use(VueRouter)
const router = new VueRouter({
	mode : "hash",
	routes:[

    
           



    {
      path:'/',
      components: require ('./components/pages/home.vue'),
      name:"home",

      meta : {
          requiresAuth:true,
          reload: true
      },
  },
  {
    path:'/1',
    components: require ('./components/pages/project_1.vue'),
    name:"project_1",
    meta : {
        requiresAuth:true
    }
},
{
  path:'/2',
  components: require ('./components/pages/project_2.vue'),
  name:"project_2",
  meta : {
      requiresAuth:true
  }
},{
  path:'/3',
  components: require ('./components/pages/project_3.vue'),
  name:"project_3",
  meta : {
      requiresAuth:true
  }
},
{
  path:'/4',
  components: require ('./components/pages/project_4.vue'),
  name:"project_4",
  meta : {
      requiresAuth:true
  }
}, 
{
  path:'/5',
  components: require ('./components/pages/test.vue'),
  name:"test",
  meta : {
      requiresAuth:true
  }
}, {
  path:"*",
  redirect:"/"
}
  ],
  scrollBehavior: function (to) { //pour la navigation dans MENU avec "to" et "h"
    if (to.hash) {
      return {
        selector: to.hash
      }
    }
  }
  
  // ,
  //  scrollBehavior (to, from, savedPosition) { //ne fonction pas quand "to.hash" est activé
  //   return { x: 0, y: 0 }
  // }
})


/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
}).$mount('#app')



